import React, { memo, useCallback, useMemo } from 'react'

import Dropdown from 'src/forms/Dropdown'
import DateRangePicker from 'src/indyComponents/DateRangePicker'

import styles from './styles.module.scss'

const FilterRaw = ({ fields = [] }) => {
  const getComponent = useCallback((field) => {
    const { type, label, onChange, ...otherProps } = field
    switch (type) {
      case 'date_range': {
        return (
          <div className={styles.col} key={type}>
            <div className={styles.label}>{label}</div>
            <DateRangePicker onChangeDate={onChange} />
          </div>
        )
      }
      case 'affiliate':
      case 'brands':
      case 'id_brands':
      case 'geo': {
        return (
          <div className={styles.col} key={type}>
            <div className={styles.label}>{label}</div>
            <Dropdown onChange={(value) => onChange(value)} {...otherProps} />
          </div>
        )
      }

      default: {
        return null
      }
    }
  }, [])

  const renderFilter = useMemo(() => {
    return fields.map((field) => {
      return getComponent(field)
    })
  }, [fields, getComponent])

  return (
    <div className={styles.box}>
      <div className={styles.boxEmpty} />
      <div className={styles.boxContent}>
        <div className={styles.root}>
          <div className={styles.row}>{renderFilter}</div>
        </div>
      </div>
    </div>
  )
}

export const Filter = memo(FilterRaw)
export default Filter
