import { useMapActions, useMapState } from 'src/hooks'
import { offersThunks } from 'src/thunks'
import { offersSelectors } from 'src/selectors'

export const useMappedState = () => {
  return useMapState((state) => {
    return {
      data: offersSelectors.getOffers(state),
      isLoading: offersSelectors.getIsLoading(state),
    }
  })
}

export const useMappedActions = () => {
  return useMapActions({
    loadOffers: offersThunks.loadOffers,
  })
}
