import React, { memo, useMemo, useState, useEffect, useCallback } from 'react'
import cn from 'classnames'
import { NavLink } from 'react-router-dom'

import { commonConstants } from 'src/constants'
import { getDictionary, getDictionaryByCode } from 'src/services'

import Filter from 'src/indyComponents/Filter'
import PageLayout from 'src/components/PageLayout'
import FullLoader from 'src/components/FullLoader'

import { useMappedActions, useMappedState } from './bindings'
import styles from './styles.module.scss'

const { BRANDS, COUNTRIES, METRICS } = commonConstants

const OffersRaw = () => {
  const { data, isLoading } = useMappedState()
  const actions = useMappedActions()

  const [filter, setFilter] = useState({
    country: [],
    brand: [],
    idBrand: [],
  })

  useEffect(() => {
    actions.loadOffers(filter)
  }, [actions, filter])

  const renderItems = useMemo(() => {
    if (!data) {
      return null
    }
    return data.map((offer) => {
      const { id, info, metrics } = offer
      return (
        <div key={id} className={styles.col}>
          <NavLink
            to={{
              pathname: `offers/${id}`,
              state: { title: info.title },
            }}
            className={styles.item}
          >
            <div
              className={styles.head}
              style={{
                backgroundImage: `url(${info.preview})`,
              }}
            ></div>
            <div className={styles.metrics}>
              <div className={styles.metricItem}>
                <div className={styles.metricLabel}>{getDictionaryByCode(METRICS, 'EPC')}</div>
                <div className={styles.metricValue}>{metrics.EPC}</div>
              </div>
              <div className={styles.metricItem}>
                <div className={styles.metricLabel}>{getDictionaryByCode(METRICS, 'EPL')}</div>
                <div className={cn(styles.metricValue, styles.metricValueGray)}>{metrics.EPL}</div>
              </div>
            </div>
            <div className={styles.body}>
              <div className={styles.title}>{info.title}</div>
              <div className={styles.descr}>{info.summary}</div>
            </div>
          </NavLink>
        </div>
      )
    })
  }, [data])

  const onChangeGeo = useCallback(
    (value) => {
      const nextCountry = value.map((item) => item.id)
      setFilter({ ...filter, country: nextCountry })
    },
    [filter, setFilter],
  )

  const onChangeBrands = useCallback(
    (value) => {
      const nextBrands = value.map((item) => item.id)
      setFilter({ ...filter, brand: nextBrands })
    },
    [filter, setFilter],
  )

  const onChangeIdBrands = useCallback(
    (value) => {
      const nextIdBrands = value.map((item) => item.id)
      setFilter({ ...filter, idBrand: nextIdBrands })
    },
    [filter, setFilter],
  )

  const filterFields = useMemo(() => {
    return [
      {
        type: 'geo',
        label: 'Geo',
        placeholder: 'All geo',
        options: getDictionary(COUNTRIES),
        onChange: onChangeGeo,
      },
      {
        type: 'brands',
        label: 'Brands',
        options: getDictionary(BRANDS),
        hasSearch: true,
        placeholderSearch: 'Search brand...',
        placeholder: 'All brands',
        onChange: onChangeBrands,
      },
      {
        type: 'id_brands',
        label: 'ID brand',
        options: getDictionary(BRANDS),
        hasSearch: true,
        placeholderSearch: 'Search id brand...',
        placeholder: 'All ID',
        onChange: onChangeIdBrands,
      },
    ]
  }, [onChangeGeo, onChangeBrands, onChangeIdBrands])

  if (isLoading) {
    return <FullLoader />
  }

  return (
    <PageLayout pageTitle={'Offers'}>
      <Filter fields={filterFields} />
      <div className={styles.box}>
        <div className={styles.row}>{renderItems}</div>
      </div>
    </PageLayout>
  )
}

export const Offers = OffersRaw
export default memo(Offers)
