import React, { useState, memo, useRef } from 'react'
import moment from 'moment'
import DateRangePicker from 'react-bootstrap-daterangepicker'
import cn from 'classnames'

import { ReactComponent as CalendarIcon } from 'src/assets/images/icons/calendar.svg'

import styles from './styles.module.scss'

const DateRangePickerRaw = ({ onChangeDate }) => {
  const myRef = useRef()
  const [isActive, setIsActive] = useState(false)

  const [state, setState] = useState({
    from: moment().subtract(29, 'days'),
    to: moment(),
  })

  const { from, to } = state

  const handleCallback = (from, to) => {
    setState({ from, to })
    onChangeDate({ from, to })
  }

  const onHide = () => {
    setIsActive(false)
  }

  const onShow = () => {
    setIsActive(true)
  }

  const label = from.format('MMMM D, YYYY') + ' - ' + to.format('MMMM D, YYYY')

  return (
    <div>
      <DateRangePicker
        ref={myRef}
        onHide={onHide}
        onShow={onShow}
        autoApply={true}
        initialSettings={{
          startDate: from.toDate(),
          endDate: to.toDate(),
          autoApply: true,
        }}
        onCallback={handleCallback}
      >
        <div id="reportrange" className={styles.input}>
          <CalendarIcon
            className={cn(styles.icon, {
              [styles.iconActive]: isActive,
            })}
          />
          <div className={styles.label}>{label}</div>
        </div>
      </DateRangePicker>
    </div>
  )
}

export const DatePicker = memo(DateRangePickerRaw)
export default DatePicker
